h1, h2, h3, h4, h5, h6 {
	clear: both;
}
h1{
	font-size: 4rem;
	line-height: 1.15;
	margin:0.5rem 0;

}
h2{
	font-size: 3.6rem;
	line-height: 1.15;
	margin:0.5rem 0;

}
h3{
	font-size: 3rem;
	line-height: 1.15;
	margin:0.5rem 0;
}
h4{
	font-size: 2.4rem;
	line-height: 1.15;
	margin:0.5rem 0;
	font-weight: normal;

}
h5 {
	font-size: 2rem;
	font-weight: 500;
	margin:0.5rem 0;
}